#Staff {
  display: flex;
  padding: 30px 0px;
  box-sizing: border-box;
  width: 100%;
  margin: 0 auto;
}
#Staff .hide {
  display: none;
}
.Slider {
  width: 34%;
  background-color: #F4F6F8;
  min-height: 84vh;
  position: relative;
}
.Slider .role {
  padding-bottom: 60px;
  position: relative;
}
.Slider .addRoleBtn {
  width: 100%;
  height: 52px;
  line-height: 52px;
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #EBEBEB;
  font-size: 20px;
  font-weight: 500;
  color: #666666;
}
.Slider .hide {
  display: none;
}
.Slider .search {
  padding: 30px 4.02%;
}
.Slider .ant-tabs-bar {
  border: none;
}
.Slider .ant-tabs-nav-wrap {
  width: 90%;
  margin: 0 auto;
}
.Slider .ant-tabs-nav-wrap div {
  width: 100%;
}
.Slider .ant-input-affix-wrapper .ant-input-suffix {
  left: 12px;
  right: auto;
}
.Slider .ant-input-affix-wrapper .ant-input:not(:last-child) {
  padding-right: 0;
  padding-left: 30px;
  outline: none;
}
.Slider .ant-input:focus {
  outline: none;
  border: 1px solid #d9d9d9;
  box-shadow: none;
}
.Slider .tab {
  width: 100%;
}
.Slider .tab .ant-tabs-nav .ant-tabs-tab {
  margin: 0;
  width: 50%;
  text-align: center;
  height: 48px;
  line-height: 48px;
  padding: 0;
  box-sizing: border-box;
}
.Slider .tab .ant-tabs-nav .ant-tabs-tab a {
  display: block;
  font-size: 16px;
  color: #333333;
  letter-spacing: 2px;
}
.Slider .tab .ant-tabs-tab:first-child {
  border-radius: 4px 0 0 4px;
}
.Slider .tab .ant-tabs-tab:last-child {
  border-radius: 0 4px 4px 0;
}
.Slider .tab .ant-tabs-nav {
  background-color: #E7EAEF;
}
.Slider .tab .ant-tabs-ink-bar {
  display: none !important;
}
.Slider .tab .ant-tabs-tab-active {
  color: #fff;
  background-color: #F1948E;
}
.Slider .tab .ant-tabs-nav .ant-tabs-tab-active a {
  color: #fff;
}
.Slider .organize .ant-menu-item-selected > a,
.Slider .organize .ant-menu-item-selected > a:hover {
  color: #666;
}
.Slider .organize .ant-collapse,
.Slider .organize .ant-collapse-item,
.Slider .organize .ant-collapse-content {
  border: none;
}
.Slider .organize .ant-collapse > .ant-collapse-item > .ant-collapse-header {
  padding-left: 30px;
}
.Slider .organize .ant-collapse > .ant-collapse-item > .ant-collapse-header .ant-collapse-arrow {
  margin: 0;
}
.Slider .organize .ant-collapse-content,
.Slider .organize .ant-collapse > .ant-collapse-item > .ant-collapse-header {
  color: #666;
}
.Slider .organize .ant-collapse > .ant-collapse-item-active > .ant-collapse-header {
  color: #fff;
  background-color: #F1948E;
}
.Slider .organize .ant-menu {
  background-color: #F4F6F8;
}
.Slider .organize .ant-menu .ant-menu-item-selected {
  color: #666666;
}
.Slider .organize .ant-menu > .thirdDept .a {
  padding-left: 30px;
}
.Slider .organize .ant-menu .ant-menu .ant-menu .thirdDept .a {
  padding-left: 60px;
}
.Slider .organize .ant-menu .a {
  display: inline-block;
  width: 100%;
  cursor: pointer;
}
.Slider .organize .ant-menu .ant-menu-submenu-active,
.Slider .organize .ant-menu .ant-menu-submenu-title:hover,
.Slider .organize .ant-menu .ant-menu-submenu-title,
.Slider .organize .ant-menu .ant-menu-submenu-title:hover {
  color: #666666;
}
.Slider .organize .ant-menu .ant-menu-submenu-active .firstImg,
.Slider .organize .ant-menu .ant-menu-submenu-title:hover .firstImg,
.Slider .organize .ant-menu .ant-menu-submenu-title .firstImg,
.Slider .organize .ant-menu .ant-menu-submenu-title:hover .firstImg {
  width: 20px;
  height: 20px;
  vertical-align: middle;
  margin-top: -1.2%;
  margin-right: 6px;
}
.Slider .organize .ant-menu .ant-menu-submenu-open .ant-menu-submenu-title:hover {
  color: #fff;
}
.Slider .organize .ant-menu .ant-menu-submenu-open .ant-menu-submenu-title:hover .a {
  color: #fff;
}
.Slider .organize .ant-menu .ant-menu-submenu-open .ant-menu-submenu-active .ant-menu-submenu-title:hover {
  color: #666666;
}
.Slider .organize .ant-menu .ant-menu-submenu-open .ant-menu-submenu-active .ant-menu-submenu-title:hover .a {
  color: #666666;
}
.Slider .organize .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background-color: #F4F6F8;
}
.Slider .organize .ant-menu-vertical .ant-menu-item::after,
.Slider .organize .ant-menu-vertical-left .ant-menu-item::after,
.Slider .organize .ant-menu-vertical-right .ant-menu-item::after,
.Slider .organize .ant-menu-inline .ant-menu-item::after {
  border: none;
}
.Slider .organize .ant-menu-inline .ant-menu-submenu > div {
  padding-left: 7px;
  box-sizing: border-box;
}
.Slider .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-title {
  height: 52px;
  line-height: 52px;
  margin: 0;
  padding: 0;
  position: relative;
}
.Slider .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-title .firsttDept {
  padding-left: 10px !important;
  padding-right: 0;
  font-size: 16px;
}
.Slider .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-title .a {
  color: #666;
  padding-right: 0;
  font-size: 16px;
  padding-left: 30px;
}
.Slider .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-title .firsttDept {
  color: #fff;
}
.Slider .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-title .otherImg {
  width: 17px;
  height: 16px;
  vertical-align: middle;
  margin-top: -1.2%;
  margin-right: 6px;
}
.Slider .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-title span {
  font-size: 16px;
}
.Slider .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-title span i {
  font-size: 16px;
}
.Slider .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-title .ant-menu-submenu-arrow {
  display: none;
}
.Slider .organize .ant-menu-inline .ant-menu-submenu .link-active {
  background-color: #F1948E !important;
  color: #fff !important;
}
.Slider .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-open .link-active i {
  transform: rotate(90deg);
  transition: all 0.2s linear 0;
}
.Slider .organize .ant-menu-light > .ant-menu-submenu > .ant-menu-submenu-title > div {
  padding-left: 10px!important;
}
.Slider .organize .ant-menu-item {
  font-size: 16px;
}
.Slider .ant-menu {
  background-color: #F4F6F8;
}
.Slider .ant-menu .ant-menu-item {
  margin: 0;
}
.Slider .ant-menu .ant-menu-item .roleName {
  color: #666;
  position: relative;
}
.Slider .ant-menu .ant-menu-item .roleName::before {
  content: '';
  width: 100%;
  height: 0;
  border-bottom: 1px solid #e7e7e7;
  position: absolute;
  bottom: 0;
  left: 0;
}
.Slider .ant-menu .ant-menu-item-selected .roleName::before {
  content: '';
  width: 100%;
  height: 0;
  border-bottom: 1px solid #F1948E;
  position: absolute;
  bottom: 0;
  left: 0;
}
.Slider .ant-menu.ant-menu-dark .ant-menu-item-selected,
.Slider .ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected {
  background-color: #F1948E;
}
.Slider .ant-menu.ant-menu-dark .ant-menu-item-selected .roleName,
.Slider .ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected .roleName {
  color: #fff;
}
.ant-drawer-body .addRole {
  padding: 3% 8.38% 4% 8.91%;
  box-sizing: border-box;
}
.ant-drawer-body .addRole .head {
  padding: 3% 8.38% 4% 8.91%;
  padding-left: 0;
  padding-right: 0;
}
.ant-drawer-body .addRole .power_title {
  padding-bottom: 3.7%;
}
.ant-drawer-body .addRole .ant-col {
  padding-bottom: 3.7%;
}
.ant-drawer-body .addRole .formItem .ant-form-item-control-wrapper {
  width: 100%;
}
.ant-drawer-body .ant-checkbox-checked .ant-checkbox-inner {
  background-color: #F1948E;
  border-color: #F1948E;
}
.ant-menu-item-disabled > .ant-menu-submenu-title,
.ant-menu-submenu-disabled > .ant-menu-submenu-title {
  cursor: pointer;
  color: #fff !important;
}
.ant-menu-item-disabled,
.ant-menu-submenu-disabled {
  color: #666 !important;
  background: #F1948E;
  border-color: transparent !important;
  cursor: pointer;
}
